import api from '../../utils/api.js';
import util from '../../utils/util.js';
const app = getApp();
export default {
	data() {
		return {
			type: 3,
			school: '',
			grade: '',
			list: [],
			data: {
				type: 3,
				action: '',
				name: '同学',
				images: [],
				word: {
					name: '',
					birthday: '',
					qq: '',
					father_phone: '',
					mother_phone: '',
					address: '',
					hobby: '',
					motto: '',
					to_me: '',
					to_classmate: ''
				},
				file: '',
				f_id: '',
				id: ''
			},
			maxNum: 4
		};
	},
	onLoad(options) {
		const that = this;
		// 添加还是编辑
		if (options.id) {
			api.photoDetail({
				data: {
					id: options.id
				},
				success: function(res) {
					res.action = "edit";
					res.type = that.type;
					res.word = JSON.parse(res.word);
					res.images = [];
					that.data = res;
				}
			});
		}else{
			this.data.action = "add";
		}
		api.index({
			success: function(res) {
				that.school = res.school;
				that.grade = res.grade;
			}
		});
		// 获取模板列表
		api.keeperCover({
			data: {
				id: this.type
			},
			success: function(res) {
				that.list = res.list;
			}
		});
	},
	methods: {
		upImage() {
			const that = this;
			let num = this.maxNum - this.data.images.length;
			api.uploadImage({
				num: num,
				success: function(res) {
					that.data.images = that.data.images.concat(res);
				}
			});
		},
		del(e) {
			let index = e.currentTarget.dataset.index;
			this.data.images.splice(index, 1);
		},
		upTemp() {
			const that = this;
			if (!this.data.word.name) {
				util.msg('自己上传需要填写您的姓名');
				return false;
			}
			that.data.name = this.data.word.name;
			let data = JSON.parse(JSON.stringify(that.data));
			data.word = JSON.stringify(data.word);
			api.uploadImage({
				num: 1,
				success: function(result) {
					uni.uploadFile({
						url: app.globalData.host + 'common/upload',
						header: {
							'content-type': 'multipart/form-data',
							token: app.globalData.token
						},
						filePath: result[0],
						name: 'file',
						formData: data,
						success: function(res) {
							let data = JSON.parse(res.data);
							if (data.code == 200) {
								util.success(
									'上传成功',
									function() {
										uni.reLaunch({
											url: '../albumList/albumList'
										});
									},
									1200
								);
							} else {
								util.msg(data.msg);
							}
						},
						fail: function(res) {
							console.log(res);
						}
					});
				}
			});
		},
		start() {
			if (!this.data.word.name) {
				util.msg('请填写您的姓名');
				return false;
			}
			if (!this.data.word.birthday) {
				util.msg('请填写您的生日');
				return false;
			}
			if (!this.data.word.qq) {
				util.msg('请填写您的QQ号');
				return false;
			}
			if (!this.data.word.father_phone || !this.data.word.mother_phone) {
				util.msg('请填写父母电话');
				return false;
			}
			if (!this.data.word.address) {
				util.msg('请填写家庭地址');
				return false;
			}
			if (!this.data.word.hobby) {
				util.msg('请填写您的爱好');
				return false;
			}
			if (!this.data.word.motto) {
				util.msg('请填写您的座右铭');
				return false;
			}
			if (!this.data.word.to_me) {
				util.msg('请填写想对自己说的话');
				return false;
			}
			if (!this.data.word.to_classmate) {
				util.msg('请填写想对同学说的话');
				return false;
			}
			if (this.data.images.length < this.maxNum) {
				util.msg('还需要上传' + (this.maxNum - this.data.images.length) + '张图片');
				return false;
			}
			this.data.name = this.data.word.name;
			app.globalData.templateData = this.data;
			// 开始设计
			uni.navigateTo({
				url: '../templateList/templateList?id=' + this.type
			});
		}
	},
	onUnload() {
		app.globalData.templateData = {};
	}
};
